<template>
	<div v-if="userInfo" >
		<el-card class="box-card" style="width:1080px">
			<div slot="header" class="clearfix">
				<span class="card" style="float: left;">主页</span>

			</div>
			<div class="text item">

				<el-col :span="15">

					<div class="demo-basic--circle">
						<div class="block">
							<el-avatar shape="square" :size="150"></el-avatar>
							<div>
								<span class="name-z">用户名:{{loginname}}</span>
							</div>
							<div>
								<p class="name-z">积分:{{userInfo.score}}</p>

							</div>
							<div>
								<p class="name-z">
									<a :href="'/#/collect/'+loginname">{{collect_topics.length}}个话题被收藏</a>
								</p>

							</div>
							<p class="name-z">
								<a :href="'https://github.com/'+userInfo.githubUsername">
									@ {{ userInfo.githubUsername }}
								</a>
							</p>
							<p class="name-z">
								注册时间:2018-09-19
							</p>
						</div>

					</div>
				</el-col>
			</div>
		</el-card>
		<!--最近创建的话题-->
		<el-card class="box-card" style="margin-top:20px ;">
			<div slot="header" class="clearfix">
				<span class="card" style="float: left;">最近创建的话题</span>

			</div>
			<div class="text item">

				<el-col :span="15">

					<div class="demo-basic--circle">
						<div class="block">
							<div v-for="(item,index) in userInfo.recent_topics" :key="index"  >
								<el-link :href="'#/topic/'+item.id">{{item.title}}</el-link>
								<el-divider></el-divider>
								
							</div>
						</div>

					</div>
				</el-col>
			</div>
		</el-card>
		<!--最近创建的话题-->
		<el-card class="box-card" style="margin-top:20px ;">
			<div slot="header" class="clearfix">
				<span class="card" style="float: left;">最近参与的话题</span>

			</div>
			<div class="text item">

				<el-col :span="15">

					<div class="demo-basic--circle">
						<div class="block">
							<div v-for="(item,index) in userInfo.recent_replies" :key="index"  >
								<el-link :href="'#/topic/'+item.id">{{item.title}}</el-link>
								<el-divider></el-divider>
							</div>
						</div>

					</div>
				</el-col>
			</div>
		</el-card>
	</div>

</template>

<script>
	//	import { Card,Group,Cell } from 'vux';
	export default {
		name: "user",
		props: ["loginname"],
		components: {
			//			Card,
			//			Cell,
			//			Group,
		},
		data() {
			return {
				userInfo: null,
				collect_topics: []
			}
		},
		beforeRouteEnter(to, from, next) {
			next(function(vm) {
				vm.$http.get("/user/" + to.params.loginname).then(function(res) {
					console.log(res.data);
					vm.userInfo = res.data.data;
				});
				//ajax请求   获取用户收藏的主题信息
				vm.$http.get("/topic_collect/" + to.params.loginname).then(function(res) {
					console.log(res.data)
					vm.collect_topics = res.data.data
				})
			})
		},
		beforeRouteUpdate(to, from, next) {
			this.$http.get("/user/" + to.params.loginname).then(function(res) {
				console.log(res.data);
				that.userInfo = res.data.data;
			});
			//获取用户收藏的主题信息
			this.$http.get("/topic_collect/" + to.params.loginname).then(function(res) {
				console.log(res.data)
				vm.collect_topics = res.data.data
			})
			next();
		}
	}
</script>

<style>
	.text {
		font-size: 15px;
		text-align: left;
	}
	
	.item {
		margin-bottom: 10px;
	}
	
	.clearfix:before,
	.clearfix:after {
		display: table;
		content: "";
		text-align: left;
	}
	
	.clearfix:after {
		clear: both;
	}
	
	.box-card {
		width: 1080px;
	}
	
	
	
	.name-z {
		/*display: inline-block;*/
		line-height: 50px;
		height: 50px;
		/*width: 50px;*/
		/*float: left;*/
	}
	
	a {
		text-decoration: none;
	}
</style>


