<template>
  <div>
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="未读消息" name="weidu">
        <el-button
          type="success"
          plain
          v-if="hasNotReadMsg && hasNotReadMsg.length > 0"
          @click.prevent="markAll"
          href
        >全部标记已读</el-button>
        <el-tag v-else>
          <i class="el-icon-info">暂无消息</i>
        </el-tag>
        <!-- 未读消息 -->
      <div v-for="(item,index) in hasNotReadMsg" :key="index"  class="text item">
        <div style="padding: 10px 10px;">
           
          <a :href="'/#/user/'+item.author.loginname">{{ item.author.loginname }}</a>回复了您的话题
          <a :href="'/#/topic/'+item.topic.id">{{ item.topic.title }}</a>
        </div>
      </div>

      </el-tab-pane>




      <el-tab-pane label="已读消息" name="yidu">
        <el-tag v-if="hasReadMsg && hasReadMsg.length > 0" >
          <i class="el-icon-success">已读消息</i>
        </el-tag>
        <div   v-for="(item,index) in hasReadMsg" :key="index"  class="text item">
        <div  style="padding: 10px 10px;">
           
          <a :href="'/#/user/'+item.author.loginname">{{ item.author.loginname }}</a>回复了您的话题
          <a :href="'/#/topic/'+item.topic.id">{{ item.topic.title }}</a>
        </div>
      </div>
      </el-tab-pane>
      
     
    </el-tabs>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeName: "weidu",
      //创建一个选择元素下标，0
      selectedTabIndex: 0,
      //已读
      hasReadMsg: null,
      //未读
      hasNotReadMsg: null
    };
  },
  //creatd  获取消息
  created: function() {
    //页面加载完成后  执行ajax请求  获取消息数据
    var that = this;
    this.$http
      .get("/messages", {
        params: {
          //get请求，获取到accesstoken值
          accesstoken: this.$store.state.accesstoken
        }
      })
      .then(function(res) {
        console.log(res.data);
        //				获取响应到的消息
        //				更新当前的数据70-71
        that.hasReadMsg = res.data.data.has_read_messages;
        that.hasNotReadMsg = res.data.data.hasnot_read_messages;
      });
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    //标记单条消息为已读
    markRead: function(mid) {
      //得到消息的id
      console.log(mid);
      var that = this;
      //执行ajax请求
      this.$http
        .post("/message/mark_one/" + mid, {
          //传入参数accesstoken
          accesstoken: this.$store.state.accesstoken
          //捕获响应
        })
        .then(function(res) {
          //判断一下响应消息，如果为真，
          if (res.data && res.data.success) {
            //标记指定消息为已读成功
            console.log("标记已读成功！");
            //把未读消息列表中标记为已读的消息删除
            that.$router.go(0);

            //在未读消息列表中删除当前记录
          }
        });
    },
    //标记全部为已读
    markAll: function() {
      var that = this;
      this.$http
        .post("/message/mark_all", {
          accesstoken: this.$store.state.accesstoken
        })
        .then(function(res) {
          if (res.data && res.data.success) {
            //标记全部消息为已读成功
            that.$router.go(0);
          }
        });
    }
  }
};
</script>

<style>
.el-tab-pane {
  text-align: left;
}
.text {
  text-align: left;
}
</style>


