<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>编辑话题</span>
    </div>
    <div class="text item">
      <el-input placeholder="新发表的话题" v-model="topic.title" clearable></el-input>
    </div>
    <div class="text item">
          <mavon-editor v-model="topic.content" :toolbars="toolbars"></mavon-editor>
      
    </div>

    <div class="text item">
      <el-button type="success" @click.native="edittopic">修改话题</el-button>
    </div>
  </el-card>
</template>

<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
  name: "edit",
  props: ["id"],
  data() {
    return {
      input: "",
      textarea: "",
      topic: {
        title: "",
        content: ""
      },

      value: "客户端测试",
      list: ["请选择", "分享", "问答", "招聘", "客户端测试"],
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true // 预览
      }
    };
  },
  components: {
   
    mavonEditor
  },
  methods: {
    edittopic: function() {
      var that = this;
      this.$http
        .post("/topic/update", {
          topic_id: this.topic.id,
          title: this.topic.title,
          content: this.topic.content,
          accesstoken: this.$store.state.accesstoken,
          tab: "dev"
        })
        .then(function(res) {
          that.$router.back();
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(function(vm) {
      vm.$http.get("/topic/" + to.params.id).then(function(res) {
        console.log(res.data);
        vm.topic = res.data.data;
      });
    });
  },
  beforeRouteUpdate(to, from, next) {
    var that = this;
    vm.$http.get("/topic/" + to.params.id).then(function(res) {
      console.log(res.data);
      that.topic = res.data.data;
    });
    next();
  }
};
</script>

<style>
.clearfix {
  text-align: left;
}
.text {
  text-align: left;
  margin-top: 15px;
}
.box-card {
  width: 1000px;
}
</style>



