<template>
	<div id="">
		Center page
	</div>
	
	

</template>

<script>
	export default {
		name: "center",
		computed: {
			loginname: function() {
				return this.$store.state.loginname;
			}
		},
		created() {
			this.$router.replace('/user/' + this.loginname)
		},
	}
</script>

<style>
 .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }
</style>


