import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import TopicList from '@/components/TopicList.vue'
import Topic from '@/components/Topic.vue'
//
import Login from '@/components/Login.vue'
import Center from '@/components/Center.vue'
import User from '@/components/User.vue'
import Logout from '@/components/Logout.vue'
import Msg from '@/components/Msg.vue'
import Create from '@/components/Create.vue'
//import Collect from '@/components/Collect.vue'
import Edit from '@/components/Edit.vue'

Vue.use(Router)

export default new Router({
routes: [
{path:'/',redirect:"/topiclist/all"},//设置重定向路由 默认加载主题列表全部
{path:'/topiclist/:name',name:"topiclist",component:TopicList,props:true}, //主题列表路由
{path:'/topic/:id',name:"topic",component:Topic,props:true}, //主题 (列表内容)详情
//
{path:'/login',name:"login",component:Login},//登录组件
{path:'/center',name:"center",component:Center},//个人中心
{path:'/user/:loginname',name:"user",component:User,props:true},// 用户中心
{path:'/logout',name:"logout",component:Logout},//退出
{path:'/create',name:"create",component:Create},//发表新话题
{path:'/msg',name:"msg",component:Msg},//消息通知
//{path:'/collect/:loginname',name:"collect",component:Collect,props:true},//收藏的话题
//
{path:'/topic/:id/edit',name:"edit",component:Edit,props:true}//编辑路由
]
})


