<template>
  <h5 v-if="isShowMsg">tuichu</h5>
</template>

<script>
export default {
  components: {
    name: "logout"
  },
  data() {
    return {
      isShowMsg: flase
    };
  },
  created() {
     // 如果用户未登录  不应该访问退出操作
    if (this.$store.state.isLogined == false) {
      this.isShowMsg = true;
      return;
    }

     var that = this;
    this.$confirm("此操作将退出登录, 是否继续?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        //实现退出操作
        //1.清空本地存储
        window.localStorage.clear();
        //2.恢复vuex中的数据 accesstoken为空 inLogined:false isShowMoreMenus:false
        this.$store.commit("mutationLogout");
        //3.页面跳转
        this.$router.push("/");

        this.$message({
          type: "success",

          message: "退出成功!"
        });
      })
      .catch(() => {
        //3.页面跳转
        this.$router.push("/");
        this.$message({
          type: "info",
          message: "已取消"
        });
      });
   
  },
  methods: {}
};
</script>

<style>
</style>



