<template>
  <div id="app">
    <el-container>
      <el-header>
        <el-menu
          :default-active="activeIndex2"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <el-image class="zz" src="https://static2.cnodejs.org/public/images/cnodejs_light.svg"></el-image>

          <el-menu-item class="nav" index="/topiclist/all">全部</el-menu-item>
          <el-menu-item index="/topiclist/good">精华</el-menu-item>

          <el-menu-item index="/topiclist/share">分享</el-menu-item>
          <el-menu-item index="/topiclist/ask">问答</el-menu-item>
          <el-menu-item index="/topiclist/job">招聘</el-menu-item>
          <el-menu-item index="/topiclist/dev">客户端测试</el-menu-item>
          <!--v-if登录-->
          <el-menu-item
            index="/"
            @click="dialogFormVisible = true"
            v-if="isLogined==false"
            style="margin-left: 500px"
          >登录</el-menu-item>
          <!--v-else-->
          <el-submenu index v-else style="margin-left: 500px">
            <template slot="title">
              {{loginname}}
              <el-badge v-if="badgeText" :value="badgeText" class="item"></el-badge>
            </template>
            <el-menu-item index="/center">
              <i class="el-icon-user-solid"></i>个人中心
            </el-menu-item>
            <el-menu-item index="/msg">
              <i class="el-icon-message-solid"></i>消息通知
            </el-menu-item>
            <el-menu-item index="/create">
              <i class="el-icon-s-comment"></i>发表话题
            </el-menu-item>

            <el-menu-item index="/logout" >
              <i class="el-icon-s-home"></i>退出

            </el-menu-item>
          </el-submenu>
          <!--弹出框-->
          <el-dialog title="Access Token：" v-model="accesstoken" :visible.sync="dialogFormVisible">
            <el-form :model="form">
              <el-form-item label="登录">
                <el-input v-model="accesstoken" autocomplete="off"></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormVisible = false">取 消</el-button>
              <el-button
                type="primary"
                @click="dialogFormVisible = false"
                @click.native="dologin()"
              >确 定</el-button>
            </div>
          </el-dialog>

          <!-- <el-alert title="accesstoken输入错误"  v-model="isShowToast" type="warning">-->
        </el-menu>
      </el-header>
      <el-container>
        <el-aside>
          <div id="aside">
            <div class="as aside1">
              <span>Conde社区</span>
            </div>
            <div class="as aside2">
          <el-image class="zz" src="https://static2.cnodejs.org/public/images/cnodejs_light.svg"></el-image>

			</div>
			 <div class="as aside2">
          <el-image class="zz" src="https://static2.cnodejs.org/public/images/cnodejs_light.svg"></el-image>

			</div>
            <div class="as aside3"></div>
          </div>
        </el-aside>
        <el-main>
          <router-view />
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import HelloWorld from "./components/HelloWorld";
import { mapState, mapGetters, mapMutations } from "vuex";
export default {
  name: "App",
  //		props: ["loginname"],
  components: {
    HelloWorld
  },
  data() {
    return {
      activeIndex2: "/all",
	  isShowMsg:true,
      accesstoken: "",
      isShowToast: false,
      toastText: "",
      dialogTableVisible: false,
      dialogFormVisible: false,
      userInfo: null,
      collect_topics: [],
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      }
    };
  },
  
  // 当前组键加载或创建完成后的回调
  created() {
    //读取localStorage数据
    var loginStatus = window.localStorage.getItem("loginStatus");
    if (loginStatus) {
      //执行用户处理登录状态
      this.mutationLogin();
      //为了再次刷新页面的时候state里面的loginname仍然有值
      this.mutationLoginname(window.localStorage.getItem("loginname"));
      this.mutationAccesstoken(window.localStorage.getItem("accesstoken"));
      this.mutationAuthorId(window.localStorage.getItem("author_id"));
    }
    //
    if (this.isLogined) {
      //用户登录才获取，错误
      //  执行ajax请求 获取未读消息
      //当页面创建以后，就开始读取消息
      this.getUnreadmMsgCount();
      var that = this;
      window.setInterval(function() {
        that.getUnreadmMsgCount();
      }, 1000 * 60 * 5); //定时获取未读消息
    }
  },
  beforeRouteEnter(to, from, next) {
    next(function(vm) {
      vm.$http.get("/user/" + to.params.loginname).then(function(res) {
        console.log(res.data);
        vm.userInfo = res.data.data;
      });
      //ajax请求   获取用户收藏的主题信息
      vm.$http.get("/topic_collect/" + to.params.loginname).then(function(res) {
        console.log(res.data);
        vm.collect_topics = res.data.data;
      });
    });
  },
  beforeRouteUpdate(to, from, next) {
    this.$http.get("/user/" + to.params.loginname).then(function(res) {
      console.log(res.data);
      that.userInfo = res.data.data;
    });
    //获取用户收藏的主题信息
    this.$http.get("/topic_collect/" + to.params.loginname).then(function(res) {
      console.log(res.data);
      vm.collect_topics = res.data.data;
    });
    next();
  },
  computed: {
    ...mapState(["topics", "isLogined", "loginname"]) //辅助函数
  },
  methods: {
    //通过ajax获取未读消息
    // 获取服务器端未读消息个数
    getUnreadmMsgCount: function() {
      var that = this;
      this.$http
        .get("/message/count", {
          params: {
            //通过get请求，请求api地址
            //当前用户信息
            accesstoken: this.$store.state.accesstoken
          }
          //补货响应
        })
        .then(function(res) {
          //修改当前textbagbe的值
          console.log(res.data.data);
          if (res.data && res.data.success) {
            that.badgeText = res.data.data;
          }
        });
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      this.$router.push("" + keyPath);
    },

    ...mapMutations([
      "mutationLogin",
      "mutationLoginname",
      "mutationAccesstoken",
      "mutationAuthorId"
    ]),
    //执行用户登陆
    dologin: function() {
      var that = this;
      this.$http
        .post("/accesstoken", {
          accesstoken: this.accesstoken
        })
        .then(function(res) {
          //接收后响应
          console.log(res.data);
          //验证登陆成功后 修改vuex中的登陆状态isLogined的值
          that.mutationLogin();
          that.mutationLoginname(res.data.loginname);
          that.mutationAccesstoken(that.accesstoken);
          that.mutationAuthorId(res.data.id);
          //vuex中的数据保存在内存中的 当前应用程序生命周期结束时 vuex中的数据就会丢失
          //用户持久化登陆 本地存储   localstorage    sessionstorage

          //保存登陆后的数据到本地存储locakStotage中
          window.localStorage.setItem("loginStatus", true);
          window.localStorage.setItem("loginname", res.data.loginname);
          window.localStorage.setItem("accesstoken", that.accesstoken);
          window.localStorage.setItem("author_id", res.data.id);
          //实现页面跳转(用户中心)user用户中心loginame谁登录的往谁的界面跳
          that.$router.push("/user/" + res.data.loginname);
        })
        .catch(function(error) {
          //catch 捕获错误
          //请求失败时，会在catch处理错误
          that.isShowToast = true;
          that.toastText = error.response.data.error_msg;
        });
    },
    open() {
      this.$prompt("请输入Access Token", "提示", {
        confirmButtonText: "确定登录",
        cancelButtonText: "取消登录",
        confirmButtonClass: "dologin",
        inputPattern: "isShowToast",
        inputErrorMessage: "Access Token格式不正确"
      })
        .then(({ value }) => {
          this.$message({
            type: "success",
            message: "Access Token: " + value
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消登录"
          });
        });
    }
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /*margin-top: 60px;*/
}

.zz {
  height: 60px;
  width: 150px;
  float: left;
  margin-left: 20px;
  margin-right: 100px;
  cursor: pointer;
}

.nav {
  margin-left: 100px;
}

#aside {
  box-shadow: 5px 5px 15px gainsboro;
  /*border: 1px solid red;*/
  width: 210px;
  height: 250px;
  margin: 20px auto;
  
}

.aside1 {
  height: 50px;
  border-bottom: 1px solid gainsboro;
}

.aside2 {
  height: 70px;
  border-bottom: 1px solid gainsboro;
  background: gray;
  line-height: 110px;
}

.aside3 {
  height: 50px;
  border-bottom: 1px solid gainsboro;
}

.aside1 span {
  line-height: 50px;
}

.aside2 p {
  line-height: 55px;
}
</style>


