<template>
  <div>
    <h1 class="hh1">{{ topic.title }}</h1>

    <el-row @click.native="collect">
      <span class="span11">
        作者：
        <a :href="'/#/user/'+topic.author.loginname">{{ topic.author.loginname }}</a>
      </span>
      <span class="span11">{{ topic.visit_count }} 次浏览</span>
      <span class="span11">来自于：{{ topic.tab }}</span>
      <el-button
        type="success"
        size="small"
        width="20px"
        v-if="topic.is_collect == false"
        @click.native="collect"
      >收藏</el-button>
      <el-button @click.native="decollect" type="success" size="small" width="20px" v-else>取消收藏</el-button>
	  <a :href="'#/topic/'+topic.id+'/edit'"
	   v-if="topic.author_id == $store.state.author_id"
	   >编辑</a>
    </el-row>
    
	
	
    <el-divider></el-divider>
    <section v-html="topic.content" style="text-align: left;"></section>
  </div>
</template>

<script>
//	import { Group , CellBox , Flexbox, FlexboxItem, WechatEmotion,XTextarea,XButton  } from "vux";
import { mapState } from "vuex";
export default {
  name: "topic",
  props: ["id"],
  components: {},
  data() {
    return {
      topic: null,
      list: ["强"],
      replyContent: ""
    };
  },
  methods: {
    //回复评论
    addReply: function(replyTab) {
      //若replyTab为1 表示为主话题进行评论
      //r若replyTab为2  表示为恢复其他评论
      this.$http
        .post("/topic/" + this.topic.id + "/replies", {
          accesstoken: this.$route.state.accesstoken,
          content: this.replyContent
        })
        .then(function(res) {
          //返回结果：{success：true,reply_id:""}
        });
    },
    //收藏主题
    collect: function() {
      var that = this;
      this.$http
        .post("/topic_collect/collect", {
          accesstoken: this.$store.state.accesstoken,
          topic_id: this.topic.id
        })
        .then(function(res) {
          console.log(res);
          //判断是否成功
          if (res.data && res.data.success) {
            //ajax修改按钮状态“收藏” -》“取消收藏
            that.topic.is_collect = true;
          }
        });
    },
    //取消收藏
    decollect: function() {
      var that = this;
      this.$http
        .post("/topic_collect/de_collect", {
          accesstoken: this.$store.state.accesstoken,
          topic_id: this.topic.id
        })
        .then(function(res) {
          console.log(res.data);
          if (res.data && res.data.success) {
            //ajax修改按钮状态“取消收藏” -》“收藏
            that.topic.is_collect = false;
          }
        });
    }
  },
  computed: {
    ...mapState(["isLogined"])
  },
  beforeRouteEnter(to, from, next) {
    next(function(vm) {
      vm.$http
        .get("/topic/" + to.params.id, {
          params: {
            accesstoken: vm.$store.state.accesstoken
          }
        })
        .then(function(res) {
          //					console.log(res.data);
          vm.topic = res.data.data;
        });
    });
  },
  beforeRouteUpdate(to, from, next) {
    var that = this;
    this.$http
      .get("/topic/" + to.params.id, {
        params: {
          accesstoken: this.$store.state.accesstoken
        }
      })
      .then(function(res) {
        console.log(res.data.data);
        that.topic = res.data.data;
      });
  }
};
</script>

<style>
.hh1 {
  margin-bottom: 10px;
}

.span11 {
  font-weight: 700;
  margin-left: 20px;
}
</style>


