<template>
	<div>

		<el-table :data="lists" @row-click="handle" style="width: 100%">

			<el-table-column label="头像" width="70">
				<template slot-scope="scope">
					<div class="red">
						<el-avatar shape="square" :size="40" :src="scope.row.author.avatar_url"></el-avatar>
					</div>
				</template>
			</el-table-column>

			<el-table-column label="阅读" width="110">
				<template slot-scope="scope">
					<el-popover trigger="hover" placement="top">
						<p>阅读量: {{ scope.row.reply_count }}</p>
						<p>阅读数: {{ scope.row.visit_count }}</p>
						<div slot="reference" class="name-wrapper">
							<el-tag size="medium">{{ scope.row.reply_count }} / {{ scope.row.visit_count }}</el-tag>

						</div>
					</el-popover>
				</template>
			</el-table-column>
			<el-table-column label="标志" width="90" v-if="$route.path=='/topiclist/all' || $route.path=='/toplist/good'">
				<template slot-scope="scope">
				<el-tag type="success" v-if="scope.row.top">置顶</el-tag>
				<el-tag type="success" v-else-if="scope.row.good">精华</el-tag>
				<el-tag type="success" v-else-if="$route.path=='/topiclist/all' || $route.path=='/toplist/good'">{{getTopicDescByTab(scope.row.tab)}}</el-tag>
			</template>
			</el-table-column>

			<el-table-column label="标题" prop="title" width="700">

			</el-table-column>
			<el-table-column label="查看">
				<template slot-scope="scope">
					<div class="red">
						<el-avatar shape="square" :size="20" :src="scope.row.author.avatar_url"></el-avatar>
						<span class="time">2018-19-19</span>
					</div>
				</template>
			</el-table-column>
		</el-table>
		<!--分页-->
		<div id="fenpage">
			<el-pagination background layout="prev, pager, next" @prev-click="freshData2" 
				
				@next-click="freshData"
				>
			</el-pagination>
		</div>

	</div>

</template>

<script>
	import { mapState, mapGetters } from "vuex";

	export default {
		name: "topiclist",
		props: ["name"],

		data() {
			return {
				lists: [],
				page: 1, //当前页数
				limit: 15, //每一页显示的主题数量
				
			};
		},
		computed: {
			...mapGetters(["getTopicDescByTab"]),
			...mapState(["topics"])
		},
		methods: {
		//下一页
		freshData: function() {
			var that = this;
			this.$http.get('/topics', {
				params: {
					tab: this.name,
					page: this.page,
					limit: this.limit
				}
			}).then(function(res) {
				if(res.data && res.data.success) {
					that.page += 1;
					that.lists = res.data.data;
					

				}
			})
		},
		//上一页
		freshData2: function() {
			var that = this;
			this.$http.get('/topics', {
				params: {
					tab: this.name,
					page: this.page,
					limit: this.limit
				}
			}).then(function(res) {
				if(res.data && res.data.success) {
					that.page=-1;
					that.lists = res.data.data;
					

				}
			})
		},
		handle(row, event, column) {
			console.log(row, event, column)

			this.$router.push('/topic/' + row.id);

		},

		deleteRow(index, rows) {
			rows.splice(index, 1);
		},
		//当单机路由链接时，实现路由跳转
		routeto: function(index) {
			//参数index表示当前点击的tab项在整个tab中的位置号（从0开始）
			console.log(index);
			//修改当前topic的selected的属性值
			//		for (let i=0; i<this.topics.length; i++){
			//			this.topics[i].selected = false;
			//		}
			//		this.topics[index].selected = true;
			//实现编程式路由跳转
			this.$router.push('/topiclist/' + this.topics[index].name);
		}

	},

	//1.ajax可以使用生命周期钩子函数
	//1.ajax可以使用生命周期钩子函数
	//2.可以使用导航守卫回调函数（更适合动态路由组件）
	beforeRouteEnter(to, from, next) {
			//进入组件之前的回调函数，还没有生成组件对象，所以不能直接使用this
			//to 表示待进入的路由组件
			//from 表示离开的组件路由
			//next 执行下一步操作

			//this.$http.get("/topics");//此处不能使用this对象
			next(function(vm) {
				//vm表示当前组件的实例对象
				vm.$http.get("/topics", {
					params: {
						tab: to.params.name,
						page: vm.page,
						limit: vm.limit
					}
				}).then(function(response) {
					console.log(response);
					if(response.data.success) {
						vm.lists = response.data.data;
					}
				})
			});
		},
		//动态路由切换时的导航守卫
		beforeRouteUpdate: function(to, from, next) {
			var that = this;
			this.page = 1;
			//可以直接使用this，表示当前组件对象
			this.$http.get("/topics", {
				params: {
					tab: to.params.name,
					page: this.page,
					limit: this.limit
				}
			}).then(function(response) {
				if(response.data.success) {
					console.log(response.data.success)
					that.lists = response.data.data; //异步不能直接使用this
				}
			});
			//执行下一步操作
			next();
		},

	}
</script>

<style>
	.el-main {
		/*margin-left: 92px;*/
	}
	
	.time {
		padding-left: 15px;
		font-size: 18px;
		line-height: 18px;
	}
	
	#fenpage {
		margin-top: 10px;
		float: left;
	}
</style>


