<template>

	<el-button type="text" v-model="accesstoken" @click="open">Access Token：登录</el-button>
</template>

<script>
	//	import { XInput, Group,XButton ,Toast} from 'vux';
	import { mapMutations } from "vuex";
	export default {
		name: "login",
		components: {

		},
		data() {
			return {
				accesstoken: '',
				isShowToast: false,
				toastText: ""
			}
		},
		methods: {
			
			//执行用户登陆
			dologin: function() {
				var that = this;
				this.$http.post("/accesstoken", {
					accesstoken: this.accesstoken
				}).then(function(res) { //接收后响应
					console.log(res.data);
					//验证登陆成功后 修改vuex中的登陆状态isLogined的值
					that.mutationLogin();
					that.mutationLoginname(res.data.loginname);
					that.mutationAccesstoken(that.accesstoken);
					that.mutationAuthorId(res.data.id);
					//vuex中的数据保存在内存中的 当前应用程序生命周期结束时 vuex中的数据就会丢失
					//用户持久化登陆 本地存储   localstorage    sessionstorage

					//保存登陆后的数据到本地存储locakStotage中
					window.localStorage.setItem("loginStatus", true);
					window.localStorage.setItem("loginname", res.data.loginname);
					window.localStorage.setItem("accesstoken", that.accesstoken);
					window.localStorage.setItem("author_id", res.data.id);
					//实现页面跳转(用户中心)user用户中心loginame谁登录的往谁的界面跳
					that.$router.push("/user/" + res.data.loginname);
				}).catch(function(error) { //catch 捕获错误
					//请求失败时，会在catch处理错误
					that.isShowToast = true;
					that.toastText = error.response.data.error_msg;
				})
			},
			open() {
				this.$prompt('请输入Access Token', '提示', {
					confirmButtonText: '确定登录',
					cancelButtonText: '取消登录',
					confirmButtonClass: 'dologin',
					inputPattern: 'isShowToast',
					inputErrorMessage: 'Access Token格式不正确'
				}).then(({
					value
				}) => {
					this.$message({
						type: 'success',
						message: 'Access Token: ' + value
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '取消登录'
					});
				});
			}
		}
	}
</script>

<style>

</style>


