<template>
  <div>
    <el-card class="box-card" style="width:1080px">
      <div style="text-align:left">
        <span>选择板块:</span>

        <el-select v-model="select" slot="prepend" placeholder="请选择">
          <el-option label="请选择" value="1"></el-option>
          <el-option label="分享" value="2"></el-option>
          <el-option label="问答" value="3"></el-option>
          <el-option label="招聘" value="4"></el-option>
          <el-option label="客户端测试" value="5"></el-option>
        </el-select>
      </div>
      <el-input placeholder="标题字数10位以上" v-model="input1" class="input-with-select"></el-input>
      <mavon-editor v-model="content" :toolbars="toolbars" @imgAdd="uploadImg" ></mavon-editor>
    </el-card>
  </div>
</template>

<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
  name: "create",
  components: {
    mavonEditor
  },
  data() {
    return {
      select: "",
      input1: "",
      title: "",
      content: "",
      value: "请选择",
      list: ["请选择", "测试", "分享", "问答", "招聘"],
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true // 预览
      }
    }
  },
  methods: {
			//上传本地图片
			uploadImg:function  (pos,$file) {
				var that = this;
				//表示当前markdown图片的序号
				console.log(pos);
				//表示上传图片的file对象
				console.log($file);
				var params = new FormData();
				params.append("smfile",file)
				//第一步，先把图片上传到云服务器（axios发送请求）
				axios.post("https://sm.ms/api/v2/upload",params,{
					//请求的配置信息(上传文件需要给出附加配置信息)
					//文件上传必须是post请求
					//文件上传必须要指定Content-type：multipart/form-data
					headers:{"Content-type":"multipart/form-data"}
				}).then(function  (res) {
				//第二部，图片上传成功后，修改markdown
					this.$refs.md.$img2Url(pos,res.data.data.url)
				})

			},
			addContent: function() {
				this.$http.post("topics", {
					title: this.title,
					content: this.content,
					tab: "dev",
					accesstoken: this.$store.state.accesstoken
				}).then(function() {
					if(res.data.seccess == true) {
						that.$router.replace("/topic/" + res.data.topic_id);
					}
				})
			}
		}
	}

</script>

<style>
.el-select .el-input {
  width: 130px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
.el-input {
  margin-top: 15px;
}
.v-note-wrapper{
  margin-top: 15px;

}
</style>


